.App {
  text-align: center;
}
#root{
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.element {
  background-color: #e78fb3;
  border-radius: 50%;
  position: relative;
  transition: 100ms;
  top: 0;
  left: 0;
}

.element-1 {
  grid-area: 5/5/6/6;
}

.element-2 {
  grid-area: 5/4/6/5;
}

.element-3 {
  grid-area: 4/4/5/5;
}

.element-4 {
  grid-area: 4/5/5/6;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }

}

@keyframes move-1{
  from {
    left: 0;
    top: 0;
    background-color: #e78fb3;
  }
  50% {
    left: 30px;
    top: 30px;
    background-color: #9656a1;
  }
  to {
    left: 0;
    top: 0;
    background-color: #e78fb3;
  }
}

@keyframes move-2{
  from {
    left: 0;
    top: 0;
    background-color: #e78fb3;
  }
  50% {
    left: -30px;
    top: 30px;
    background-color: #9656a1;
  }
  to {
    left: 0;
    top: 0;
    background-color: #e78fb3;
  }
}

@keyframes move-3{
  from {
    left: 0;
    top: 0;
    background-color: #e78fb3;
  }
  50% {
    left: -30px;
    top: -30px;
    background-color: #9656a1;
  }
  to {
    left: 0;
    top: 0;
    background-color: #e78fb3;
  }
}

@keyframes move-4{
  from {
    left: 0;
    top: 0;
    background-color: #e78fb3;
  }
  50% {
    left: 30px;
    top: -30px;
    background-color: #9656a1;
  }
  to {
    left: 0;
    top: 0;
    background-color: #e78fb3;
  }
}

@keyframes rotate{
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes eye-1{
  from {
    border-width: 3px  0;
    height: 100%;
  }
  to {
    border-width: 2px  0;
    height: 0;
  }
}

@keyframes eye-2{
  from {
    left: 0;
  }
  33% {
    left: -6px;
  }
  67% {
    left: 6px;
  }
  to {
    left: 0;
  }
}

@keyframes eye-3{
  from {
    left: 0;
  }
  33% {
    left: -2px;
  }
  67% {
    left: 2px;
  }
  to {
    left: 0;
  }
}

@keyframes mouth{
  from {
    border-width: 3px  0;
    top: -17px;
    height: 100%;
  }
  to {
    top: 0;
    border-width: 2px  0;
    height: 0;
  }
}

a:active, a:focus { outline: none; text-decoration: none; }

input, textarea {outline:none;}
input:active, textarea:active {outline:none;}
:focus {outline:none;}
textarea {resize:none;}
textarea {resize:vertical;}
textarea {resize:horizontal;}
button:active, button:focus {
  outline: none;
}
button::-moz-focus-inner {
  border: 0;
}

@keyframes appearance-right {
  from {
    opacity: 0;
    position: relative;
    left: 30px;
  }
  to {
    opacity: 1;
    position: relative;
    left: 0;
  }
}

@keyframes appearance-left {
  from {
    opacity: 0;
    position: relative;
    right: 30px;
  }
  to {
    opacity: 1;
    position: relative;
    right: 0;
  }
}

@keyframes appearance-1 {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes arm-right-bottom {
  from {
    transform: rotate(-135deg);
  }
  50% {
    transform: rotate(-185deg);
  }
  to {
    transform: rotate(-135deg);
  }
}

@keyframes arm-left-bottom {
  from {
    transform: rotate(135deg);
  }
  50% {
    transform: rotate(185deg);
  }
  to {
    transform: rotate(135deg);
  }
}


